@font-face {
// название шрифта
font-family: "PFDasGroteskPro-Regular"; 
// адрес шрифта
src: url("../fonts/PFDasGroteskPro-Regular.otf") format("truetype"); 
font-style: normal; 
font-weight: normal; 
} 
@font-face {
// название шрифта
font-family: "PFDasGroteskPro-Regular"; 
// адрес шрифта
src: url("../fonts/PFDasGroteskPro-Bold.otf") format("truetype"); 
font-style: normal; 
font-weight: bold; 
} 
$color1: #ffffff;//white for text
$color2: #1a1a1a;//Black for BG
$color3: #262626;//Dark gray for windows
$color4: #f2f2f2;//white for BG
$color6: #000000;//black for text
a{text-decoration: none!important;}
button:focus,a:focus,input:focus,select:focus,textarea:focus{outline:none!important;}
li{list-style-type: none;}
// -------------------------------------------------------------------
body{
	padding: 15px 15px 15px 15px;
	background-color: $color4;
	// div{border: 1px solid red;}
	box-sizing: border-box;
	margin: 0px;
	font-family: "PFDasGroteskPro-Regular";
	font-size: 24px;
}
img{
	width: 100%;
	max-width: 300px;
}
.content{
	width: calc(100vw - 185px);// Отступ контейнера от хедера
	min-height: 100vh;
	float: right;
	padding-bottom: 15px;
}
.row1{
	display:flex;
	height: calc(50vw - 92.5px);//Высота строки
	width: 100%;
}
.row2{
	display:flex;
	height: calc(25vw - 46.25px);//Высота строки
	width: 100%;
	.box_sm{
		height: 100%;
	}
}
.row3{
	display:flex;
	height: calc(12.5vw - 23px);//Высота строки
	width: 100%;
	.box_sm{
		height: 100%;
	}
}
.col1{
	display:flex;
	flex-wrap: wrap;
	align-content: stretch;
	width: 50%;
	height: 100%;
}
.col2{
	display:flex;
	width: 50%;
	height: 100%;
}
.col3{
	display:flex;
	flex-wrap: wrap;
	align-content: stretch;
	width: 50%;
	height: 100%;
}
.box_xs{
	width: 50%;
	height: 50%;
	display: block;
	// padding: 10px;
	box-sizing: border-box;
	border: 5px solid $color4;
	display:flex;
	flex-wrap: wrap;
	align-content: stretch;
}
.box_sm{
	width: 50%;
	height: 50%;
	display: block;
	// padding: 10px;
	box-sizing: border-box;
	border: 5px solid $color4;
}
.box_lg{
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	border: 5px solid $color4;
}
.bg{
	&:hover{
		filter: blur(1px);
		-webkit-filter: blur(1px);
		background-size:  105% 105% ;
	}
}
.human_info{
	.hide{
		display: none;
		text-align: center;
		font-size: 1.1vw;
		color: white;
		width: 100%;
		height: 30%;
		background-color: rgba(0,0,0,0.3);
		position: relative;
		top: 70%;
		span{
			position: relative;
			// top: 5px;
		}
	}
	&:hover .hide{
		display: block;
	}
}
.human_text{
	background-color: #1e1e1e;
	color: $color1;
	padding: 10px;
	font-size: 1.25vw;
}
.no_border{
	border: 0!important;
}
.map_info{
	width: 100%;
	height: 37%;
	background-color: rgba(0,0,0,0.5);
	color: $color1;
	padding: 10px;
	box-sizing: border-box;
}
.map{
	background: url("../img/company5(5).jpg")50% 50% no-repeat;
	background-size: auto;
}
