// 		bottom:  calc(-10% - 0px);
// 		left: calc(45% - 32px);
// 		width: 64px;
// 	}
// }
// Form style--------------------------------
.company_form{
	background-color: $color2;
	color: $color1;
	padding: 25px;
	input{
		background-color: $color2;
		color: $color1;
		border: none;
		border-bottom: 1px solid #707070;
		margin-top: 2px;
		font-size: 20px;
	}
	div{
		width: 100%;
	}
	span{
		float: right;
	}
	p{
		margin-top: 0px;
		margin-bottom: 40px;
	}
}
.InputName{
	margin-bottom: 30px;
	input{
		width: calc(100% - 107px);
		float: right;
	}
}
.InputEmail{
	margin-bottom: 25px;
	input{
		width: calc(100% - 203px);
		float: right;
	}
}
.InputText{
	textarea{
		width: calc(100% - 5px);
		height: calc(20vw - 73px);
		resize: none;
		background-color: $color2;
		border: 1px solid #707070;
		color: $color1;
		font-size: 16px;
		&::placeholder{
			color: $color1;
		}
	}
}
.ButtonForm{
	background-color: $color2;
	color: $color1;
	border: 1px solid $color1;
	padding: 10px 50px;
	float: right;
	margin-top: calc(3vw - 10px);
	font-size: 20px;
	&:hover{
			color: #5e5e5e;
			border: 1px solid #5e5e5e;
		}
}
// Form style--------------------------------END
// Text in the box
.text_box{
	// padding-top: 5px;
	background-color: $color6;
	border-top: 6px solid $color4;
	box-sizing: border-box;
	padding: 10px;
	font-size: 1.25vw;
	p{
		color: $color1;
		margin: 7px 0;
	}
}
.show_more{
	text-align: center;
	p{
		margin-top: calc(40% - 15px);

	}
	img{
		margin-top: 15px;
		width: 40%;
		height: auto;
	}
}

// Header---------------------------------------------
.logo{
	position: fixed;
	top: 21px;
	left: 20px;
	width: 125px;
	img{
		box-shadow: 0px -5px 70px -30px #000000;
	}
}
.buttons{
	position: fixed;
	bottom: 20px;
	left: -20px;
	ul{
		margin: 0;
	}
	a{
		color: $color6;
		display: block;
		border: 2px solid $color6;
		width: 130px;
		// height: 32px;
		padding-top: 5px;
		padding-bottom: 5px;
		margin-top: 15px;
		box-sizing: border-box;
		// white-space: nowrap;
		text-align: center;
		line-height: 14px;
		font-size: 14px;
		&:hover{
			color: #5e5e5e;
			border: 2px solid #5e5e5e;
		}
	}
}
// Header END----------------------------------------------
// Adaptive
@media (min-width: 1360px) {
	.logo{
		width: 180px;
	}
	.content{
		width: calc(100vw - 240px);// Отступ контейнера от хедера
	}
	.row1{
		height: calc(50vw - 120px);//Высота строки
	}
	.row2{
		height: calc(25vw - 60px);//Высота строки
	}
	.buttons{
		a{
			width: 180px;
		}
	}
}
@media (min-width: 1025px) {
	.content_mobile{
		display: none!important;
	}
}
@media (max-width: 1024px) {
	.content{
		display: none;
	}
	.box_xs{
		width: 33%;//Ширина Боксов
		height: calc(33vw - 61px);//Высота строки
	}
}
@media (max-width: 770px) {
	.box_xs{
		width: 50%;//Ширина Боксов
		height: calc(50vw - 92.5px);//Высота строки
	}
}
@media (max-width: 680px) {
	// .box_xs{
	// 	width: 50%;//Ширина Боксов
	// 	height: calc(50vw - 92.5px);//Высота строки
	// }
}
// Mobile style
.content_mobile{
	width: calc(100vw - 185px);// Отступ контейнера от хедера
	min-height: 90vh;
	float: right;
	padding-bottom: 15px;
	display:flex;
	flex-wrap: wrap;
	align-content: flex-start;
}
.box_xs{
	// width: 50%;//Ширина Боксов
	// height: calc(50vw - 100px);//Высота строки
	display: block;
	// padding: 10px;
	box-sizing: border-box;
	border: 5px solid $color4;
	display:flex;
	
	align-content: stretch;
}